<template>
    <div class="black-bg" v-if="modalStatus == true">
    <div class="white-bg">
      <img class="room-img" v-bind:src="roomsInfo[currentProductNo].image">
      <h4> {{ roomsInfo[currentProductNo].title }}</h4>
      <p>{{ roomsInfo[currentProductNo].content }}</p>
      <!-- <input @input="month = $event.target.value" type="text"> -->
      <input v-model.number="month" type="number">
      <p>{{ month }}개월선택함 : {{ roomsInfo[currentProductNo].price * month }}원</p>
      <span>이 물건의 허위매물 신고 건수 : {{신고수[currentProductNo]}} 건 </span>
      <button @click="$emit('resetNo')">신고건수 초기화</button>
      <Discount v-if="statusDiscount == true"/>
      <button @click="$emit('closeModal'); ">닫기</button>   
      {{statusDiscount}}
    </div>
    </div>
</template>

<script>
import Discount from './Discount.vue'

export default {
    name : "ModalView",
    data() {
        return {
            month : 3,
            
        }
    },

    props : {
        roomsInfo : Array,
        currentProductNo : Number,
        modalStatus : Boolean,
        신고수 : Array,
        statusDiscount : Boolean,

    },

    components: {
         Discount 
    },
    
    watch : {
        month(val){
            if(val >= 13){
                alert('13이상의 숫자를 입력하면 안됩니다.');
                this.month = 3;
            }else if(val % 1 != 0){
                alert('정수만 입력하세요');
                this.month = 3;
            }
            if(isNaN(parseInt(val))){
                alert('숫자만 입력하세요');
                this.month = 3;
            }
            
        }
    },

    mounted() {
        if(this.modalStatus == true){
            setInterval(()=>{
                this.$emit('closeDiscount')
        }, 5000);
        }
        
              
    },
    
    beforeUpdate() {
        if(this.month <= 2){
            alert('2개월 이하는너무 적어요.. 3개월 부터..');
            this.month = 3;
        }
        
    },
}

</script>

