<script>
import data from './assets/oneroom';
import Discount from './components/Discount.vue'
import Modal from './components/Modal.vue'
import Card from './components/Card.vue'


export default {
  name: 'App',
  data(){
    return{
      sale : '',
      modalStatus : false,
      currentProductNo : 0,
      origin : [...data],
      roomsInfo : data,
      price : [50,60,70],
      products : ['역삼동원룸','천호동원룸','마포구원룸'],
      menus : ['Home','Shop','Event'],
      신고수 : [0,0,0,0,0,0],
      statusDiscount : true,
    }
  },
  components: {
    Discount : Discount,
    Modal,
    Card,
  },

  watch:{
    
    
  },

  methods: {
    //숫자정렬
    sortTest(){
      let arr = [3,5,2];
      arr.sort();
      console.log("오름차순 정렬 :"+arr);

      arr.sort(function(a,b){
        return b-a;
      })
      console.log("내림차순 정렬 :"+arr);
      
    },

  
    sortPriceAsc(){
      this.roomsInfo.sort(function(a,b){
        return a.price - b.price;
      })
    },

    sortPriceDesc(){
      this.roomsInfo.sort(function(a,b){
        return b.price - a.price;
      })
    },

    sortTitleAsc(){
      this.roomsInfo.sort((a,b)=>{
        if(a.title < b.title ) return -1;
        if(a.title > b.title ) return 1;
        else return  0;
      })
    },

    sortTitleDesc(){
      this.roomsInfo.sort((a,b)=>{
        if(a.title < b.title ) return 1;
        if(a.title > b.title ) return -1;
        else return  0;
      })
    },

    dataOrigin(){
      this.roomsInfo = [...this.origin];
    },

  },
}
</script>

